.contact {
	border: 5px solid beige;
}

.chat {
	background-color: rgb(127 179 221 / 72%);
}

.contentL {
	position: relative;
	padding: 0px 20px;
	border-radius: 15px;
	display: table;
}

.contentR {
	position: relative;
	padding: 0px 20px;
	border-radius: 15px;
	display: table;
	margin-inline-start: auto;
}

.contentL:after {
	position: absolute;
	left: -20px;
	content: "";
	bottom: calc(15px);
	border-style: solid;
	border-width: 10px 10px 10px 10px;
	border-color: transparent rgb(127 179 221 / 72%) transparent transparent;
}

.contentR:after {
	position: absolute;
	right: -20px;
	content: "";
	bottom: calc(15px);
	border-style: solid;
	border-width: 10px 10px 10px 10px;
	border-color: transparent transparent transparent rgb(127 179 221 / 72%);
}

.wrapper {
	position: relative;
	padding: 0px 20px;
}

.infoM {
	position: relative;
	display: flex;
	justify-content: flex-end;
	margin: 0px 10px;
}

.timeM {
	line-height: normal;
	margin: 0px;
	font-size: x-small;
}

.heading {
	font-size: x-small;
	line-height: normal;
}

.infoS {
	position: relative;
	display: flex;
	margin: 0px 10px;
}

.bodyM {
	min-height: 20px;
	flex-direction: column;
	display: flex;
	word-wrap: break-word;
	overflow: hidden;
	text-overflow: ellipsis;
	margin: 0px 10px;
}