.slide {
  width: 500pt;
  left: 0pt;
}

ul {
  list-style-type: none;
  padding: 0;
}

.roundImg {
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

.without_padding {
  padding: 0;
}

#input_chat {
  bottom: 0;
}

#frame {
  margin: auto;
  width: 80%;
  background-color: rgba(255, 255, 255, .15);
  backdrop-filter: blur(5px);
  margin-top: 3%;
  margin-bottom: 3%;
  margin-left: 10%;
  margin-right: 10%;
  height: 90vh;
  white-space: pre-wrap;
}


.text {
  font-size: 20px;
  padding: 6px;
  padding-left: 40px;
  color: black;
  text-overflow: ellipsis;
}

.displayNameCont {
  text-overflow: ellipsis;
  text-align: start !important;
  width: 60%;
  white-space: nowrap;
  padding-left: 5%;
  vertical-align: -webkit-baseline-middle;
  font-size: large;
  overflow: hidden;
  padding-top: 2%;
  color: black;
}

.trigger {
  margin-bottom: 10px;
}


.topping {
  background-color: beige !important;
  border-color: beige !important;
  padding: 16px 8px;
  border-radius: 0px !important;
}

.contact {
  border: 5px solid beige;
  border-radius: 7px;
}

.bgAddContact {
  background-image: url(../photos/background.jpg);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

#closeAddNewContact {
  background-color: beige;
}

#logout {
  width: 50px;
  height: 50px;
}

#childElement {
  background-color: transparent;
  overflow-y: auto;
}

.breakWord {
  word-break: break-word;
}