  .message {
    position: absolute;
    width: -webkit-fill-available;
    height: inherit;
    overflow-y: auto;
    margin-bottom: 100px;
  }
 
  .delpadding{
    padding: 0;
    border: 0;
  }

  .delmargin{
    margin: 0;
  }
  #msgsBox {
    overflow-y: auto;
  }

  #typing {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  #t-message {
    word-break: break-word;
    inline-size: -webkit-fill-available;
    table-layout: fixed;
    width: 100%;
  }

  #in{
    height: -webkit-fill-available;
  }